<template>
  <div class="table-container">
    <el-table ref="table" v-loading="loading" :data="tableData" :border="true">
      <el-table-column
        prop="appProdInstDto.prodName"
        :label="$t('simMag.prodName')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="appProdInstDto.prodNbr"
        :label="$t('simMag.prodNbr')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="attrName"
        :label="$t('simMag.attrName')"
        show-overflow-tooltip
      >
        <template #default="props">
          <div v-for="(item, index) in props.row.appProdInstAttrDtos" :key="index">
            {{ item.attrName }}: {{ formatterValue(item) }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="appProdInstDto.createDate"
        :label="$t('simMag.effDate')"
        show-overflow-tooltip
      ></el-table-column>
    </el-table>
    <!-- <el-pagination
      v-show="tableData.length > 0"
      background
      :current-page="page.pageIndex"
      align="right"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="page.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    >
    </el-pagination> -->
  </div>
</template>

<script>
import fetch from '@/request/fetch'
export default {
  props: {
    detailInfo: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
  data() {
    return {
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      loading: false,
    }
  },
  mounted() {
    this.qryProdInstSubList()
  },
  methods: {
    async qryProdInstSubList() {
      try {
        this.loading = true
        const { ownerCustId } = this.detailInfo.appProdInstDto
        const accProdInstId = this.detailInfo.appProdInstDto.prodInstId
        const param = {
          accProdInstId,
          ownerCustId,
        }
        const res = await fetch('/prodInst/qryProdInstSubList', param, { method: 'get' })
        this.tableData = res || []
        this.page.total = res.total
        this.loading = false
      } catch (e) {
        this.loading = false
      }
    },
    formatterValue(item) {
      return item.attrValueName ? item.attrValueName : item.attrValue
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qrySimChangeHis()
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qrySimChangeHis()
    },
  },
}
</script>
<style lang="scss" scoped>
.table-container {
  margin-top: 20px;
}
</style>
