<template>
  <div class="content">
    <div class="summary">
      <div v-for="(item, index) in summaryList" :key="item.title" class="summary-item">
        <img class="summary-item-left" :src="item.imgSrc" />
        <div class="summary-item-right">
          <div class="summary-item-right-title">
            {{ item.title
            }}<a
              v-if="index === 0 && simCardType"
              :class="refreshIcon"
              style="margin-left: 10px"
              @click="refreshTotalFlowMB"
            />
          </div>
          <div class="summary-item-right-used">
            {{ item.formatter() }}
          </div>
        </div>
      </div>
    </div>
    <div class="main-info">
      <ct-section-title class="main-info-title" :title="$t('simMag.packageInfoTitle')" />
      <el-row :gutter="20">
        <el-col v-for="obj in infoList" :key="obj.prop" :span="4">
          <CtTooltip :label="obj.label" :content="itemInfoFormatter(obj)"></CtTooltip>
        </el-col>
      </el-row>
    </div>
    <SupplyOfferTable :select-package-list="selectPackageList" />
  </div>
</template>

<script>
import fetch from '@/request/fetch'
import SupplyOfferTable from './components/SupplyOfferTable'
export default {
  components: {
    SupplyOfferTable,
  },
  props: {
    detailInfo: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
  data() {
    return {
      value1: true,
      loading: false,
      mainOfferInst: {},
      planTotalUsage: '',
      totalBillUsage: '',
      totalOverUsage: '',
      selectPackageList: [],
      simCardType: 0,
      offerUsage: {},
    }
  },
  computed: {
    refreshIcon() {
      const iconClass = this.loading ? 'el-icon-loading' : 'el-icon-refresh'
      return iconClass + ' cor-blue'
    },
    offerFlag() {
      const { optionalPackageUsageList } = this.offerUsage
      if (!optionalPackageUsageList) {
        return false
      }
      return optionalPackageUsageList.length > 0 && this.selectPackageList.length > 0
    },
    summaryList() {
      return [
        {
          title: this.$t('simMag.totalUsage'),
          formatter: () => this.detailInfo.totalFlowMB || '-',
          imgSrc: require('@/assets/image/sim/sim_pkg_1.png'),
        },
        {
          title: this.$t('simMag.mainOfferUsage'),
          formatter: () => this.offerUsage.mainOfferUsageValue || '-',
          imgSrc: require('@/assets/image/sim/sim_pkg_2.png'),
        },
        {
          title: this.$t('simMag.selectOfferUsage'),
          formatter: () => this.offerUsage.optionalPackageUsageValue || '-',
          imgSrc: require('@/assets/image/sim/sim_pkg_3.png'),
        },
      ]
    },
    infoList() {
      return [
        {
          label: this.$t('simMag.offerName'),
          prop: 'offerName',
        },
        {
          label: this.$t('simMag.offerNbr'),
          prop: 'offerNbr',
        },
        {
          label: this.$t('simMag.effDate'),
          prop: 'effDate',
        },
        {
          label: this.$t('simMag.expDate'),
          prop: 'expDate',
        },
        {
          label: this.$t('simMag.statusCd'),
          prop: 'statusCd',
          formatter: (val) =>
            val === '1000'
              ? this.$t('simMag.statusCdGood')
              : this.$t('simMag.statusCdBad'), // 1000 代表开 其他值代表关
        },
      ]
    },
  },
  watch: {
    offerFlag(val) {
      if (!val) {
        return
      }
      this.selectPackageList.forEach((row) => {
        this.offerUsage.optionalPackageUsageList.forEach((item) => {
          if (item.offerInstId === row.offerInstId) {
            this.$set(row, 'usageValue', item.usageValue)
            this.$set(row, 'totalUsageValue', item.totalUsageValue)
            this.$set(row, 'remainValue', item.remainValue)
          }
        })
      })
    },
  },
  mounted() {
    this.qryOfferUsageByProdInst() // 主套餐和可选包使用量
    this.qryMainOfferInstByProdInst() // 主套餐信息
    this.qrySelectPackageByProdInst() // 可选包
    this.qrySimCardType() // 是否显示刷新按钮
    this.qrySimMonthFlowUsage() // 获取总使用量
  },
  methods: {
    itemInfoFormatter(obj) {
      const des = obj.formatter
        ? obj.formatter(this.mainOfferInst[obj.prop])
        : this.mainOfferInst[obj.prop] ?? '-'
      return des
    },
    async qryOfferUsageByProdInst() {
      const param = { prodInstId: this.detailInfo.appProdInstDto.prodInstId }
      const res = await fetch('/offerInst/qryOfferUsageByProdInst', param, {
        method: 'get',
      })
      this.offerUsage = res ?? {}
    },
    async qrySimCardType() {
      const param = { prodInstId: this.detailInfo.appProdInstDto.prodInstId }
      const res = await fetch('/prodInst/qrySimCardType', param, {
        method: 'get',
      })
      this.simCardType = res
    },
    async qryMainOfferInstByProdInst() {
      try {
        const param = { prodInstId: this.detailInfo.appProdInstDto.prodInstId }
        const res = await fetch('/offerInst/qryMainOfferInstByProdInst', param, {
          method: 'get',
        })
        this.mainOfferInst = res
      } catch (e) {}
    },
    async qrySelectPackageByProdInst() {
      try {
        const param = { prodInstId: this.detailInfo.appProdInstDto.prodInstId }
        const res = await fetch('/offerInst/qrySelectPackageByProdInst', param, {
          method: 'get',
        })
        this.selectPackageList = res
      } catch (e) {}
    },
    // 获取总使用量
    async qrySimMonthFlowUsage() {
      const params1 = {
        msisdn: this.detailInfo.appProdInstDto.accNum,
        prodInstId: this.detailInfo.appProdInstDto.prodInstId,
        custId: this.detailInfo.appProdInstDto.useCustId,
        addressId: this.detailInfo.appProdInstDto.addressId,
      }
      const res = await fetch('/prodInst/qrySimMonthFlowUsage', params1, {
        method: 'post',
      })
      this.$emit('refreshTotalFlowMB', res.totalFlowMB)
    },
    // 刷新总使用量
    async refreshTotalFlowMB() {
      const params1 = {
        msisdn: this.detailInfo.appProdInstDto.accNum,
        prodInstId: this.detailInfo.appProdInstDto.prodInstId,
        custId: this.detailInfo.appProdInstDto.useCustId,
        addressId: this.detailInfo.appProdInstDto.addressId,
      }
      this.loading = true
      const res1 = await fetch('/prodInst/qrySimMonthFlowUsage', params1, {
        method: 'post',
      }).finally(() => (this.loading = false))
      this.loading = true
      const params2 = {
        msisdn: this.detailInfo.appProdInstDto.accNum,
        iccid: this.detailInfo.iccid,
        monthUsage: res1.totalFlowMB ?? '',
        addressId: this.detailInfo.appProdInstDto.addressId,
      }
      const res2 = await fetch('/offerInst/refreshRealTimeFlow', params2, {
        method: 'get',
      }).finally(() => (this.loading = false))
      // this.$emit('refreshTotalFlowMB', res2.realTimeMonthUsage)
      this.$emit('refreshTotalFlowMB', res1.totalFlowMB)
    },
  },
}
</script>

<style lang="scss" scoped>
.content {
  background-color: #efefef;
  margin-top: 20px;
}
.summary {
  display: flex;
  padding: 20px;
  padding-top: 0px;
  background-color: white;

  &-item {
    padding: 20px;
    width: 280px;
    height: 100px;
    background: #f4f4f4;
    margin-right: 20px;
    display: flex;
    &-left {
      width: 22px;
      height: 22px;
      margin-top: -3px;
    }
    &-right {
      margin-left: 10px;
      display: flex;
      flex-direction: column;
      &-title {
        font-weight: 500;
        font-size: 14px;
        color: #333333;
      }
      &-used {
        margin-top: 20px;
        font-size: 24px;
        height: 24px;
        color: #333333;
        vertical-align: baseline;
      }
      &-unit {
        font-size: 12px;
        vertical-align: baseline;
      }
    }
  }
}
.main-info {
  margin-top: 20px;
  padding: 20px;
  background-color: white;
  &-title {
    margin-bottom: 20px;
  }
}
</style>
