<template>
  <div class="content">
    <div class="summary">
      <el-row :gutter="20" style="width: 100%">
        <el-col v-for="obj in infoList" :key="obj.prop" :span="4">
          <CtTooltip :label="obj.label" :content="itemInfoFormatter(obj)"></CtTooltip>
        </el-col>
      </el-row>
    </div>
    <div class="container-bottom">
      <ct-section-title :title="$t('simMag.basicInfoTitle')" />
      <Function :detail-info="detailInfo"></Function>
    </div>
    <div v-if="false" class="container-bottom">
      <ct-section-title :title="$t('simMag.identityRecord')" />
      <Identity :detail-info="detailInfo"></Identity>
    </div>
  </div>
</template>

<script>
import { statusOptions } from '@/utils/constant'
import Function from './components/function'
import Identity from './components/identity'
export default {
  components: {
    Function,
    Identity,
  },
  props: {
    detailInfo: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
  data() {
    return {
      info: {},
    }
  },
  computed: {
    needRealNameAuth() {
      return this.detailInfo.appProdInstDto.needRealNameAuth === 1
    },
    infoList() {
      const list = [
        {
          label: 'ICCID',
          prop: 'iccid',
        },
        {
          label: 'MSISDN',
          prop: 'msisdn',
          formatter: (row) => row.appProdInstDto.accNum,
        },
        {
          label: 'IMSI',
          prop: 'ctgImsi',
        },
        {
          label: this.$t('simMag.simStatus'),
          formatter: (row) => this.rowFormatter(row.appProdInstDto.statusCd),
        },
        {
          label: this.$t('simMag.addressId'),
          formatter: (row) => row.appProdInstDto?.addressName ?? '-',
        },
        {
          label: this.$t('simMag.cust'),
          formatter: (row) => row.appCustomerDto?.custName ?? '-',
        },
        {
          label: this.$t('simMag.openDate'),
          formatter: (row) => row.appProdInstDto.createDate,
        },
        {
          label: this.$t('simMag.actDate'),
          formatter: (row) => row.appProdInstDto.actDate,
        },
      ]
      let list2 = []
      if (this.needRealNameAuth) {
        list2 = [
          {
            label: this.$t('simMag.identityStatus'),
            formatter: (row) =>
              row.appProdInstDto.realNameAuthStatus === 1
                ? this.$t('simMag.identityStatus1')
                : this.$t('simMag.identityStatus0'),
          },
          {
            label: this.$t('simMag.identityTime'),
            formatter: (row) => row.appProdInstDto.realNameAuthDate,
          },
          {
            label: this.$t('simMag.identityUserName'),
            formatter: (row) => row.appProdInstDto.realNameAuthUserName,
          },
        ]
      }
      return [...list, ...list2]
    },
  },
  methods: {
    itemInfoFormatter(obj) {
      const des = obj.formatter
        ? obj.formatter(this.detailInfo)
        : this.detailInfo[obj.prop] ?? '-'
      return des
    },
    rowFormatter(val) {
      const item = statusOptions.find((item) => item.value === val)
      if (item) {
        return this.$t('simMag.' + item.key)
      }
      return val
    },
  },
}
</script>
