  background-color: #efefef;
  margin-top: 20px;
  // padding: 20px;
  padding-top: 0px;
  ::v-deep .el-col:not(:nth-last-child(-n + 2)) {
    margin-bottom: 25px;
  }

  .summary {
    display: flex;
    padding: 20px;
    padding-top: 0px;
    background-color: white;
  }
}

.container-bottom {
  margin-top: 20px;
  background-color: white;
  padding: 20px;
}
</style>
